.container__items {
  border: none;
  cursor: pointer;
  background: #262E38;
  border-radius: 50%;
  padding: 1rem 1.4rem;
  color: #7C8798;
}

.container__items2 {
  border: none;
  cursor: pointer;
  background: #7C8798;
  border-radius: 50%;
  padding: 1rem 1.4rem;
  color: white;
}

.container__items:hover {
  background: #FC7614 !important;
  color: white !important;
}

@media screen and (max-width: 600px) {
  .container__items,
  .container__items2 {
    padding: .7rem 1rem;
  }
}