@font-face {
  font-family: "Overpass";
  src: url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Poppins:ital,wght@0,400;0,700;1,400;1,700&display=swap');
}

* {
  color: white;
  box-sizing: border-box;
}

.container {
  margin: 25vh auto 0 auto;
  display: flex;
  flex-direction: column;
  width: 412px;
  height: 416px;
  background-color: hsl(216, 12%, 8%);
  border-radius: 30px;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 2rem;
}

.container__star {
  padding: 1rem;
  background: #262E38;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container__survey {
  flex: 1;
}

.container__survey h2 {
  font-weight: 700;
  font-size: 28px;
  line-height: 35px;
}

.container__survey p {
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: #969FAD;
}

.container__rating {
  flex: 1;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.container__button {
  cursor: pointer;
  margin-top: 1.5rem;
  flex: 1;
  width: 100%;
  background: #FC7614;
  border-radius: 22.5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container__button button {
  background: #FC7614;
  border: none;
  letter-spacing: 2px;
  font-size: 15px;
  line-height: 19px;
  font-weight: 700;
  cursor: pointer;
}

.container__button:hover,
.container__button:hover > button {
  cursor: pointer;
  background: white;
  color: #FC7614;
}

/* AFTER SUBMIT */

.container__illustration {
  width: 100%;
  display: flex;
  justify-content: center;
}

.container__user-choice {
  background: #262E38;
  padding: .5rem 1.5rem;
  margin: 2rem auto 0 auto;
  border-radius: 22.5px;
}

.container__user-choice span {
  color: #FC7614;
  font-size: 15px;
}

.container__appreciation {
  text-align: center;
}

.container__appreciation h3 {
  font-size: 28px;
  line-height: 35px;
  font-weight: 700;
  color: #FFF;
}

.container__appreciation p {
  font-size: 15px;
  line-height: 24px;
  color: #969FAD;
}


@media screen and (max-width: 600px) {
  .container {
    width: 327px;
    height: 360px;
  }
  .container__survey h2 {
    font-size: 24px;
    line-height: 30px;
  }
  .container__survey p {
    font-size: 14px;
    line-height: 22px;
  }
  .container__button {
    padding: .8rem 0;
  }
  .container__star {
    padding: .8rem;
  }
  /* AFTER SUBMIT */
  .container__user-choice {
    margin: 1rem auto;
  }
  .container__user-choice span {
    font-size: 14px;
    line-height: 22px;
  }
  .container__appreciation h3 {
    font-size: 24px;
    line-height: 30px;
    margin: 0;
  }
  .container__appreciation p {
    font-size: 14px;
    line-height: 22px;
  }
}